main#registerSection{
    background-color: var(--backRegisterColor);
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    section#registerForm{
        width: 30%;
        height: 85%;
        background-color: var(--backMainRegisterColor);
        border-radius: 2rem;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
        display: flex;
        position: relative;
        flex-direction: column;
        align-items: center;
        img{
            width: 5rem;
            margin-top: 3rem;
            &:hover{
                filter: saturate(170%);
                transition: all 300ms ease;
            }
        }
        h1{
            color: black;
            margin: 1rem 0 2rem 0;
        }
        div{ //form
            display: flex;
            justify-content: center;
            flex-direction: column;
            div.input{
                display: flex;
                flex-direction: column;
                align-items: center;
                position: relative;
                justify-content: center;
                margin-bottom: 1rem;
                span{
                    position: absolute;
                    left: 0;
                    z-index: 10;
                    padding: .2rem;
                    &#showInputSpan{
                        margin-bottom: 1rem;
                    }
                }
                input{
                    border: none;
                    border-radius: 0;
                    width: 12rem;
                    height: 2.5rem;
                    padding: 0 2rem;
                    &:focus{
                        outline: none;
                    }
                    &.wrong{
                        border: .1rem solid red;
                    }
                }
                div#password{
                    #passwordContainer{
                        #showBTN.clicked{
                            color: var(--backRegisterColor);
                        }
                    }
                    div.progress{
                        width: 100%;
                        border-radius: 0;
                        #progress-bar{
                            height: .3rem !important;
                        }
                    }
                }
            }
            #loginRedirect{
                position: absolute;
                margin-bottom: 2rem;
                bottom: 0;
            }
            button{
                background-color: var(--backRegisterColor);
                color: #fff;
                width: 75%;
                height: 2rem;
                margin: 0 auto;
                border: none;
                border-radius: .4rem;
                &:hover{
                    filter: brightness(120%);
                    transition: all 300ms ease;
                }
            }
            &#paso2{
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                .fileDrop{
                    text-align: left;
                    width: 80%;
                    height: 5rem;
                    margin: 2rem 0;
                }
                div#PESTEL {
                    margin: 1rem 0 2rem 0;
                    height: fit-content;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    input{
                        width: 1rem;
                        height: 1rem;
                    }
                    label {
                        font-size: 1rem;
                        margin: 0 0 0 0.5rem;
                    }
                }
                button{ //submit
                    width: 30%;
                    height: 3rem;
                }
            }
            &#successful{
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                margin-top: 4rem;
                width: 70%;
                height: 50%;
                p{
                    margin-bottom: 2rem;
                }
                a{
                    background-color: var(--backRegisterColor);
                    color: #fff;
                    width: 60%;
                    text-decoration: none;
                    padding: .5rem 0 .5rem;
                    margin: 0 auto;
                    border: none;
                    border-radius: .4rem;
                    &:hover{
                        filter: brightness(120%);
                        transition: all 300ms ease;
                    }
                }
            }
        }
    }
}

body.dark{
    main#registerSection{
        background-color: var(--backRegisterColorDark);
        section#registerForm{
            background-color: var(--backMainRegisterColorDark);
        }
        section#registerForm{
            h1{
            color: #fff;
            }
            div{ //form
                div.input{
                    input{
                        &.wrong{
                            border: .2rem solid rgb(255, 0, 0);
                        }
                    }
                    span{
                        color: #000;
                    }
                }
            }
        }
    }
}