main#NotFound {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  font-family: "Poppins";
  background: linear-gradient(45deg, #8500ff, #5acaff);
}
#container {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(./assets/p404.png), #151729;
  box-shadow: 0 14pz 30px rgba(0, 0, 0, 0.5);
  .content {
    max-height: 600px;
    text-align: center;
    h2 {
      font-size: 15vw;
      margin: 0;
      color: #fff;
      line-height: 1em;
      user-select: none;
    }
    h4 {
      position: relative;
      font-size: 1.5em;
      margin-bottom: 20px;
      color: #111;
      background-color: #fff;
      font-weight: 300;
      font-family: "Poppins-Light";
      padding: 10px 20px;
      display: inline-block;
      font-family: "Poppins-Light";
    }
    p {
      color: #fff;
      font-size: 1.2em;
    }
    a {
      position: relative;
      display: inline-block;
      padding: .5em 1.2em;
      background-color: var(--primaryColor);
      color: #fff;
      font-size: 1.3em;
      text-decoration: none;
      margin-top: 25px;
      border-radius: 25px;
      transition: all 300ms ease;
      font-family: "Poppins-Bold";
    }
    a:hover {
      filter: contrast(70%);
    }
  }
}
